@tailwind base;
@tailwind components;
@tailwind utilities;

html {
    scroll-behavior: smooth;
}

header ul li {
    @apply px-3;
}

.bg-primary {
    background: rgb(123, 3, 221);
}

.bg-secondary {
    background: rgb(242, 233, 244);
}

.mobile-nav {
    @apply fixed top-10 left-0 bg-gray-800 w-full h-1/6;
}

.mobile-nav li {
    @apply w-full h-full text-center border-b-2;
}

.btn {
    @apply bg-primary px-5 py-2 font-bold hover:border-2 rounded;
}

.nav-link {
    @apply hover:text-gray-300 transition-colors duration-200;
}
@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(30px); }
  100% { opacity: 1; transform: translateY(0); }
}

.animated-text {
  opacity: 0;
  animation: fadeIn 3s ease forwards;
}

.animated-text-delayed {
  animation-delay: var(--delay, 0s);
}

